import styled from "styled-components";
import {useEffect, useRef} from "react";
import Quill from "quill";
import "quill/dist/quill.snow.css";

const EditorBlock = styled.div`
  margin-top: 5rem;
`;

const TitleInput = styled.input`
    margin-bottom: 1rem;
`;

const QuillWrapper = styled.div`
  margin-bottom: 1rem;
  /*     padding  */
  .ql-editor {
    padding: 0;
    min-height: 320px;
    font-size: 1.125rem;
    line-height: 1.5;
  }.ql-editor.

  ql-blank::before { /*        */
    left: 0px;
  }
`;

const Editor = ({title, content, imageUrls, onChangeEditor}) => {
    const quillElement = useRef(null); // Quill  DivElement 
    const quillInstance = useRef(null); // Quill  

    useEffect(() => {
        quillInstance.current = new Quill(quillElement.current, {
            theme: 'snow',
            placeholder: ' .',
            modules: {
                toolbar: [
                    [{header: [1, 2, 3, 4, 5, 6, false]}],
                    ['bold', 'italic', 'underline', 'strike'],
                    [{list: 'ordered'}, {list: 'bullet'}],
                    ['blockquote', 'code-block'],
                    // [{script: 'sub'}, {script: 'super'}],
                    ['link', 'image'],
                    [{color: []}, {background: []}],
                    [{align: []}],
                    // ['clean'],
                ],
            },
        });

        const quill = quillInstance.current;
        quill.on('text-change', () => {
            onChangeEditor({key: 'content', value: quill.root.innerHTML});
        });
    }, [onChangeEditor]);

    const onChangeTitle = e => {
        onChangeEditor({key: 'title', value: e.target.value});
    };

    return (
        <EditorBlock>
            <TitleInput placeholder=" ."
                        value={title}
                        onChange={onChangeTitle}
            />
            <QuillWrapper>
                <div ref={quillElement}/>
            </QuillWrapper>
        </EditorBlock>
    );
};

export default Editor;