import Button from "../common/Button";
import styled from "styled-components";
import {Link} from "react-router-dom";

const AuthFormBlock = styled.div`
  align-items: center; /*    */
  display: flex; /*     */
  flex-direction: column; /*     */
  margin-top: 200px;

  div {
    margin-bottom: 30px;
  }

  h3 {
    margin-bottom: 10px;
  }
`;

const StyledInput = styled.input`
  width: 100%;
  border: 0.5px solid darkgrey;
  border-radius: 2.5px;
  font-size: 1rem;
  padding: 0.25rem 0 0.25rem 0.1rem;
`;

const Footer = styled.div`
  margin-top: 30px;
  text-align: center;
  font-size: 0.7rem;
`;

const textMap = {
    login: '', // 
    signUp: '',
}; // 

const AuthForm = ({type, form, onChange, onSubmit}) => {
    const text = textMap[type];

    return (
        <AuthFormBlock>
            <h2>{text}</h2>
            <form onSubmit={onSubmit}>
                <div>
                    <h3></h3>
                    <StyledInput name="email"
                                 placeholder=""
                                 value={form.email}
                                 onChange={onChange}
                    />
                </div>
                <div>
                    <h3></h3>
                    <StyledInput name="password"
                                 placeholder=""
                                 type="password"
                                 value={form.password}
                                 onChange={onChange}
                    />
                </div>
                {type === 'signUp' && (
                    <>
                        <div>
                            <h3> </h3>
                            <StyledInput name="checkPassword"
                                         placeholder=" "
                                         type="password"
                                         value={form.checkPassword}
                                         onChange={onChange}
                            />
                        </div>
                        <div>
                            <h3></h3>
                            <StyledInput name="name"
                                         placeholder=""
                                         value={form.name}
                                         onChange={onChange}
                            />
                        </div>
                        <div>
                            <h3></h3>
                            <StyledInput name="phoneNumber"
                                         placeholder="010 - XXXX - XXXX"
                                         value={form.phoneNumber}
                                         onChange={onChange}
                            />
                        </div>
                        <div>
                            <h3>URL</h3>
                            <StyledInput name="referenceUrl"
                                         placeholder="URL"
                                         value={form.referenceUrl}
                                         onChange={onChange}
                            />
                        </div>
                        <div>
                            <h3></h3>
                            <StyledInput name="part"
                                         placeholder=""
                                         value={form.part}
                                         onChange={onChange}
                            />
                        </div>
                        <div>
                            <h3></h3>
                            <StyledInput name="organization"
                                         placeholder=""
                                         value={form.organization}
                                         onChange={onChange}
                            />
                        </div>
                        <div>
                            <h3>  </h3>
                            <StyledInput name="motto"
                                         placeholder="  "
                                         value={form.motto}
                                         onChange={onChange}
                            />
                        </div>
                    </>
                )}
                <Button fullwidth="true">{text}</Button>
            </form>
            <Footer>
                {type === 'login' ? (
                    <p> ? <Link to="/auth/signup" style={{color: " #4BC75F"}}></Link> </p>
                ) : (
                    <p> ? <Link to="/auth/login" style={{color: " #4BC75F"}}></Link> </p>
                )}
            </Footer>
        </AuthFormBlock>
    );
};

export default AuthForm;